<script>
export default {
   data(){
     return{
       show:false,
       title:'标题'
     }
  },
  methods:{
    showAlert(){
      this.$dialog.alert({
        title:'Alert！',
        content:'警告content!!是否继续?'
      }).then(res => {
        alert(res)
      }).catch(rej => {
        alert(rej)
      })
    },
    showConfirm(){
      this.$dialog.confirm({
        title:'Alert！',
        content:'确认content!!是否继续?',
      }).then(res => {
        alert(res)
      }).catch(rej => {
        alert(rej)
      })
    }
  }
}
</script>


